import React, { useEffect, useContext } from 'react'
import AppContext from '../contextAPI/AppContext'
import scrollToElement from 'scroll-to-element'
import SpeechBubble from '../components/SpeechBubble/SpeechBubble'
import Introduction from '../components/Introduction/Introduction'
import Narrator from '../components/NarratorText/NarratorText'
import SegmentData from '../components/SegmentData/SegmentData'
import 'primereact/resources/themes/nova-light/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';

export default ({pageContext: {segment}}) => {
  const receivedAppContext = useContext(AppContext);
  
  // let scrollData = location.state
  useEffect(() => {
    if (typeof window !== undefined) {
      
      if (receivedAppContext.searchID !== '') {
        const id = `#${receivedAppContext.searchID}`
        scrollToElement(id, {
            offset: -65,
            ease: 'inOutCube',
            duration: 1500
        });
        receivedAppContext.updateSearchID('')
      }
    }
  }, [])

  return (
    <div>
      {/* segments start with an array of block nodes (so they have a length), but intros start with an object "document"  */}
      {(segment.content) ? (
        <div>
          <div>
            <SegmentData 
              colors={segment.colors}
              sources={segment.sources}
              repeatedWords={segment.repeatedWords}
            />
          </div>
          {segment.content.map(block => {
            // const SVBData = {
            //   color: block.data.color,
            //   source: block.data.source
            // }
            if (block.data.color === 'black') {
              return (<Narrator 
                sourceData={block.data}
                inlines={block.nodes} />
                )
            } else {
              return (<SpeechBubble 
                sourceData={block.data}
                inlines={block.nodes} />
                )
            }
          })}
        </div>
    ) : (
        <Introduction content={segment.document.nodes} />
      )}
    </div>
  )}